<template>
    <div class="app">
        <div class="nav">
            <div v-for="(item,i) in navlist" :key="i" @click="handlenav(item)">{{ item.name }}</div>
            
        </div>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
    data() {
        return {
            navlist: [{
                name:'页面一',
                path: '/'
            }, {
                name: '页面二',
                path: '/ces'
            }]
        }
    },
    methods:{
        handlenav(item) {
            this.$router.push(item.path)
        }
    }
}
</script>
<style lang="less">
.app {
    width: 100%;
    height: 100%;
}
.nav {
    position: absolute;
    left: 20px;
    top: 20px;
    > div {
        line-height: 35px;
        cursor: pointer;
        border-radius: 20px;
        height: 40px;
        line-height: 40px;
        background: #fff;
        padding: 0 30px;
        margin-bottom: 20px;
        box-shadow: 0 0 5px rgba(0,0,0,.1);
        transition: .5s all ease;
        &:hover {
            background: blueviolet;
            color: #fff;
        }
    }
}
</style>
  